-->
<script>
import { promoterLevelList } from './api.js';

export default {
  data() {
    return {
      list: []
    };
  },
  methods: {
    routerLevelEditor() {
      this.$router.push({
        path: '/promoter/level-editor'
      });
    },
    levelList() {
      promoterLevelList().then(response => {
        this.list = response;
      });
    }
  },
  mounted() {
    this.levelList();
  },
  render() {
    const self = this;
    return (
      <div class="le-main">
        <el-button type="primary" onClick={self.routerLevelEditor}>
          添加分销商等级
        </el-button>
        <el-table data={self.list} class="le-card" row-class-name="le-table-row">
          <el-table-column label="等级权重" prop="level" />
          <el-table-column label="等级名称" prop="name" />
          <el-table-column
            label="升级方式"
            scopedSlots={{
              default: scope => {
                const update_type = scope.row.update_type;
                const is_auto = scope.row.is_auto;
                if (is_auto === 0) {
                  return '不允许自动升级';
                }
                if (update_type === 1) {
                  return '满足任意条件';
                } else if (update_type === 2) {
                  return '满足全部条件';
                }
              }
            }}
          />
          <el-table-column
            label="分销商数"
            scopedSlots={{
              default: scope => {
                return <span class="le-theme--color">{scope.row.promoter_count}</span>;
              }
            }}
          />
          <el-table-column
            label="一级佣金比例"
            scopedSlots={{
              default: scope => {
                return `${parseInt(scope.row.first) / 100}%`;
              }
            }}
          />
          <el-table-column
            label="二级佣金比例"
            scopedSlots={{
              default: scope => {
                return `${parseInt(scope.row.second) / 100}%`;
              }
            }}
          />
          <el-table-column
            label="三级佣金比例"
            scopedSlots={{
              default: scope => {
                return `${parseInt(scope.row.third) / 100}%`;
              }
            }}
          />
          <el-table-column
            label="操作"
            scopedSlots={{
              default: scope => {
                return (
                  <div class="le-table-operate flex align-center">
                    {(() => {
                      if (scope.row.type) {
                        return [
                          [<el-button type="text">编辑</el-button>],
                          [<span class="le-line" />],
                          [<el-button type="text">删除</el-button>]
                        ];
                      } else {
                        return [<el-button type="text">编辑</el-button>];
                      }
                    })()}
                  </div>
                );
              }
            }}
          />
        </el-table>
      </div>
    );
  }
};
</script>

<style lang="less" scoped>
@import './index.less';

.le-card ::v-deep {
  margin-top: 24px;
  .le-table-row:extend(.font-family-yahei) {
    font-size: 14px;
    font-weight: 400;
    color: #262626;
    height: 52px;
  }
}
</style>
